  display: flex;
  background-color: #c9c9c9;
  height: 44px;
  position: relative;
}
.vux-tab button {
  padding: 0;
  border: 0;
  outline: 0;
  background: 0 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
.vux-tab .vux-tab-item {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
